<template>
  <div id="app">
    <van-swipe 
      indicator-color="white" 
      :vertical="true"
      :loop="false"
    >
      <van-swipe-item><One /></van-swipe-item>
      <van-swipe-item><Two /></van-swipe-item>
      <van-swipe-item><Three /></van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import One from './components/one';
import Two from './components/two';
import Three from './components/three';
export default {
  name: 'App',
  components: {
    One,
    Two,
    Three
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
  user-select: none;
}
html {
  overflow: hidden;
  width: 100%;
  height: 100%;
}
body {
  height: 100%;
}
#app {
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.van-swipe {

  height: 100%;
}
</style>



